@import url(https://cdnjs.cloudflare.com/ajax/libs/meyer-reset/2.0/reset.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.css);
@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700);


.app {
    background: #209cee;
    padding: 30px;
    height: 100vh;
    display: flex;
    flex-direction: column;
  }
  
  .todo-list {
    background: #e8e8e8;
    border-radius: 4px;
    padding: 5px;
    max-width: 600px;
    box-shadow: 3px 3.5px 7px 3px  rgba(0, 0, 0, 0.9);
  }
  
  .todo {
    background: #fff;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
    padding: 3px 10px;
    font-size: 20px;
    margin-bottom: 6px;
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow:hidden;
  }
@font-face{
    font-family: 'Pixel';
    src: url(/React_Firebase/static/media/Pixel-LCD-7.e2897552.woff) format('woff');
}
header {
  z-index: 9;
  position: absolute;
  top: 0;
  width: 100%;
  background: #710808;
}

header h1 {
  color: #ffff;
  font-size: 50px;
  margin: 20px 0px;
  display: flex;
  justify-content: center;
}

header form {
  margin: 20px 0px;
  display: flex;
  justify-content: center;
}

header form input {
  padding-left: 5px;
  outline: none;
  color: #ffff;
  width: 200px;
  border-radius: 5px;
  background-color: #710808;
  border: 3px solid #ffff;
}

header form input::-webkit-input-placeholder {
  color: #ffff;
  margin-left: 2px;
}

header form input:-ms-input-placeholder {
  color: #ffff;
  margin-left: 2px;
}

header form input::-ms-input-placeholder {
  color: #ffff;
  margin-left: 2px;
}

header form input::-moz-placeholder {
  color: #ffff;
  margin-left: 2px;
}

header form input::placeholder {
  color: #ffff;
  margin-left: 2px;
}

header form button {
  background-color: #ffff;
  color: black;
  outline: none !important;
  font-size: 18px;
  margin: 0px 15px;
  width: 80px;
  border-radius: 8px;
  border: 1px solid #ffff;
}

.result {
  margin-top: 100px;
  overflow: hidden;
}

.result .result1 .col-md-4 .recipe_box {
  margin: 20px 10px;
  background-color: #F2F2F2;
  border-radius: 5px;
  box-shadow: 10px 10px #710808;
}

.result .result1 .col-md-4 .recipe_box img {
  width: 620px;
  height: 250px;
  border-radius: 5px;
}

.result .result1 .col-md-4 .recipe_box .recipe_text {
  margin-top: 8px;
  margin-left: 10px;
}

.result .result1 .col-md-4 .recipe_box .recipe_text h5 {
  font-weight: 500;
}

.result .result1 .col-md-4 .recipe_box .recipe_text hr {
  margin: 11px auto;
  width: 170px;
  border: 1px solid grey;
}

.result .result1 .col-md-4 .recipe_box .recipe_text button {
  margin: 14px 0px;
  background-color: #710808;
  color: white;
  padding: 7px;
  outline: none !important;
  border: none;
  border-radius: 5px;
}

.recipe {
  background-color: #EDEDED;
}

.recipe .recipebox {
  background-color: #E0E0E0;
  padding: 30px 0px;
}

.recipe .recipebox h1 {
  display: flex;
  justify-content: center;
  margin-bottom: 12px;
  font-size: 50px;
}

.recipe .recipebox .recipe_desc p {
  display: flex;
  justify-content: center;
  padding: 15px 0px;
}

.recipe .recipebox .recipe_desc button {
  display: flex;
  margin: 0 auto;
  background-color: black;
  color: white;
  padding: 5px 10px;
  border: 1px solid black;
}

.recipe .recipebox .recipe_desc button a {
  color: white;
  outline: none !important;
}

.body {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}
