.app {
    background: #209cee;
    padding: 30px;
    height: 100vh;
    display: flex;
    flex-direction: column;
  }
  
  .todo-list {
    background: #e8e8e8;
    border-radius: 4px;
    padding: 5px;
    max-width: 600px;
    box-shadow: 3px 3.5px 7px 3px  rgba(0, 0, 0, 0.9);
  }
  
  .todo {
    background: #fff;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
    padding: 3px 10px;
    font-size: 20px;
    margin-bottom: 6px;
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow:hidden;
  }